@import url(https://fonts.googleapis.com/css2?family=DM+Sans:ital,wght@0,400;0,500;0,700;1,400;1,500;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: 'DM Sans', 'Inter', sans-serif;
  font-weight: 400;
  /* overflow: hidden; */
}

.nopost {
  text-align: center;
}

.alert {
  min-height: 60px;
  max-width: 900px;
  background: #fff4ea;
  border-radius: 2;
  margin: 50px auto;
  display: -webkit-flex;
  display: flex;
  justify-items: center;
}
.alert p {
  margin: auto;
}
@media screen and (max-width: 900px) {
  .alert {
    margin: 50px 30px;
  }
}
@media screen and (max-width: 500px) {
  .alert p {
    margin: 10px;
  }
  .alert {
    margin-top: 10px;
  }
}

.form {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  margin-top: 50px;
}
.search_form {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  border: 1px solid #000;
  height: 39px;
}
.search_form input {
  -webkit-flex: 1 1;
          flex: 1 1;
  border: none;
  outline: none;
  margin-left: 10px;
}
.search_form p {
  margin-right: 10px;
  justify-self: center;
  -webkit-align-self: center;
          align-self: center;
}

fieldset {
  display: -webkit-flex;
  display: flex;
  /* height: 40px; */
}
legend {
  text-align: left;
  font-size: 10px;
  /* height: 30px; */
}
.sort_select {
  border: none;
  outline: none;
  /* margin-top: -5px; */
  margin-bottom: -5px;
  -webkit-flex: 1 1;
          flex: 1 1;
  font-size: 1rem;
}
.sort_out {
  padding-right: 20px;
}
.sort_form {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

@media screen and (max-width: 900px) {
  .form {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .search_form {
    margin: 20px 40px;
    justify-items: center;
  }
  .sort_out {
    padding-right: 5px;
  }
}
@media screen and (max-width: 500px) {
  .form {
    margin-top: 10px;
  }
  .search_form {
    margin-bottom: 10px;
    margin-left: 30px;
  }
  .sort_form {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  fieldset {
    margin: 5px 30px;
    justify-items: center;
  }
  .sort_by {
    margin-left: 30px;
  }
}

/* width */
body::-webkit-scrollbar {
  width: 10px;
}

/* Track */
body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}

/* Handle */
body::-webkit-scrollbar-thumb {
  background: #3f3e3e;
  border-radius: 10px;
}

/* Handle on hover */
body::-webkit-scrollbar-thumb:hover {
  background: #3f3e3e;
}
.loading {
  text-align: center;
  font-size: 50px;
}
.template_header {
  color: #3f3e3e;
  text-align: left;
  display: -webkit-flex;
  display: flex;
  margin: 0 auto;
  padding: 10px;
  max-width: 900px;
}
.template_header h2 {
  margin-right: auto;
  font-size: 14px;
}
.template_body {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 40px;
  max-width: 900px;
  margin: 0 auto;
}
.template_body_container {
  background: #fff;
  box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.08);
  border-radius: 2px;
  -webkit-align-self: flex-start;
          align-self: flex-start;
}
.template_body_details h1 {
  font-weight: 500;
  text-transform: capitalize;
}
.template_body_details p {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
}
.template_body_container:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  transition: all 0.3s linear;
}
.template_body_details {
  background: #fff;
  font-weight: normal;
  color: #333447;
  font-family: cursive;
  text-align: left;
  padding: 10px;
}
.template_body_details h1 {
  font-size: 20px;
}
.template_body_details p {
  font-size: 1rem;
  color: #3f3e3e;
}
.template_body_link {
  background: #f9f9f9;
  text-align: left;
  font-size: 1rem;
  padding: 10px;
}
.template_body_link a {
  text-decoration: none;
  color: #08bd37;
}
@media screen and (max-width: 800px) {
  .template_body {
    grid-template-columns: repeat(2, 1fr);
  }
  .template_body_container {
    margin: 10px;
  }
}
@media screen and (max-width: 500px) {
  .template_body {
    grid-template-columns: 1fr;
  }
  .template_body_container {
    margin: 10px 30px;
  }

  .template_header {
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-left: 20px;
  }
  .template_header h2 {
    margin-bottom: 10px;
  }
}

.pagination {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin-top: 20px;
  color: #3f3f3f;
}
.page-btn {
  width: 10px !important;
  height: 10px !important;
}
.pagination button {
  height: 20px;
  background: #fff;
  border: none;
  border-radius: 3px;
  margin-top: 20px;
  color: #3f3f3f;
}
.pages {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.pages p {
  margin-left: 10px;
  margin-right: 10px;
  margin-top: 20px;
  color: #3f3f3f;
}
.pages button {
  height: 20px;
  background: #fff;
  border: 1px solid #3f3f3f;
  border-radius: 3px;
  margin-top: 20px;
  color: #3f3f3f;
}
.pages > .total_pages {
  border: none;
}
.prev-btn.disabled {
  pointer-events: none;
  box-shadow: none;
  color: #999;
}

